.main {
	position: relative;
	background-position: center top;
	background-repeat: no-repeat;
	flex: 1 0 0;
	padding-top: 90px;
	&-bg {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-position: center top;
			object-fit: none;
		}
	}
}
.first {
	position: relative;
	padding-top: 60px;
	&-inner {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	&-content {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		width: 550px;
	}
	&-link {
		position: relative;
		display: inline-flex;
		padding-top: 56px;
		a {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			background: rgba($colorWhite, 0.08);
			flex: 1 0 0;
			min-width: 170px;
			height: 60px;
			//border: 1px solid rgba($colorWhite, 0.03);
			border-radius: 20px;
			padding: 10px 20px;
			&:before {
				content: '';
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background: linear-gradient(to right, rgba($colorWhite, 1) 0%, rgba($colorWhite, 1) 100%) border-box;
				-webkit-mask: linear-gradient($colorWhite 0 0) padding-box, linear-gradient($colorWhite 0 0);
				-webkit-mask-composite: xor;
				mask-composite: exclude;
				border: 1px solid transparent;
				border-radius: 20px;
				opacity: 0.03;
			}
			&:first-child {
				background: linear-gradient(to right, rgba($colorWhite, 0) 0%, rgba($colorWhite, 0.08) 100%);
				&:before {
					background: linear-gradient(to right, rgba($colorWhite, 0) 0%, rgba($colorWhite, 1) 100%) border-box;
				}
			}
			&:last-child {
				background: linear-gradient(to right, rgba($colorWhite, 0.08) 0%, rgba($colorWhite, 0) 100%);
				&:before {
					background: linear-gradient(to right, rgba($colorWhite, 1) 0%, rgba($colorWhite, 0) 100%) border-box;
				}
			}
			&:nth-child(2) {
				&:after {
					content: '';
					position: absolute;
					background: linear-gradient(to right, rgba($colorBlue, 0) 0%, rgba($colorBlue, 0.3) 50%, rgba($colorBlue, 0) 100%);
					bottom: 0;
					left: 15px;
					right: 15px;
					height: 1px;
				}
			}
			&:not(:last-child) {
				margin-right: 4px;
			}
			img {
				max-height: 28px;
			}
		}
	}
	&-card {
