.partners {
	position: relative;
	&-inner {
		position: relative;
	}
	&-group {
		position: relative;
		padding-top: 52px;
	}
	&-tabs {
		position: relative;
		display: flex;
		overflow: hidden;
		&__box {
			position: relative;
			height: 52px;
			min-width: 185px;
			border-radius: 40px;
			cursor: pointer;
			&:before {
				content: '';
				position: absolute;
				background: $colorDarkBg;
				top: 0;
				left: 0;
				width: 100%;
				height: 100px;
				border-radius: 40px;
				pointer-events: none;
			}
			&:hover {
				.partners-tabs__box {
					&-img {
						img {
							filter: brightness(1);
							opacity: 1;
						}
					}
					@media screen and (hover: hover) {
						&-arrow {
							&:before {
								transform: translate(-50%, -50%) scale(1);
								transition: transform 0.5s cubic-bezier(0.1, 0, 0.3, 1);
							}
						}
					}
				}
			}
			&.active {
				z-index: 1;
				&:before {
					background: $colorWhite;
				}
				.partners-tabs {
					&__box {
						&-tape {
							&:before {
								box-shadow: -8px 58px 0px 5px $colorWhite;
							}
							&:after {
								box-shadow: -8px 58px 0px 5px $colorWhite;
							}
						}
						&-img {
							img {
								filter: brightness(1);
								opacity: 1;
							}
						}
					}
				}
			}
			&:first-child {
				.partners-tabs {
					&__box {
						&-tape {
							&:before {
								content: none;
							}
						}
					}
				}
			}
 			&-tape {
				&:before {
					content: '';
					position: absolute;
					top: -22px;
					left: -70px;
					width: 74px;
					height: 74px;
					border-radius: 50%;
					box-shadow: -8px 58px 0px 5px $colorDarkBg;
					transform: rotate(-40deg);
					pointer-events: none;
				}
				&:after {
					content: '';
					position: absolute;
					top: -22px;
					right: -70px;
					width: 74px;
					height: 74px;
					border-radius: 50%;
					box-shadow: -8px 58px 0px 5px $colorDarkBg;
					transform: rotate(28deg);
					pointer-events: none;
				}
			}
			&-img {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				text-align: center;
				height: 52px;
				img {
					max-height: 24px;
					filter: brightness(0);
					opacity: 0.3;
					transition: filter 0.5s, opacity 0.5s;
				}
			}
			&-arrow {
				position: absolute;
				display: none;
				align-items: center;
				justify-content: center;
				background: $colorText;
				top: 50%;
				right: 16px;
				width: 40px;
				height: 40px;
				color: $colorWhite;
				font-size: 20px;
				border-radius: 50%;
				transform: translateY(-50%);
				&:before {
					content: '';
					position: absolute;
					background: $colorBlue;
					top: 50%;
					left: 50%;
					width: calc(100% + 1px);
					height: calc(100% + 1px);
					border-radius: 50%;
					transform: translate(-50%, -50%) scale(0);
				}
			}
		}
	}
	&-content {
		position: relative;
		background: $colorWhite;
		border-radius: 0 40px 40px 40px;
		z-index: 1;
	}
	&-box {
		position: relative;
		flex-direction: row-reverse;
		justify-content: space-between;
		background: $colorWhite;
		padding: 24px 24px 36px 36px;
		border-radius: 0 40px 40px 40px;
		overflow: hidden;
		&__map {
			position: absolute;
			top: 16px;
			left: 0;
			width: 1235px;
			margin-left: -95px;
			transform: translateX(-50%);
		}
		&__head {
			position: relative;
			align-self: flex-end;
			width: 380px;
		}
		&__info {
			position: relative;
			font-size: 40px;
			line-height: 1;
			letter-spacing: -0.03em;
			&:not(:last-child) {
				margin-bottom: 48px;
			}
			b, strong {
				color: $colorBlue;
				font-weight: 400;
			}
		}
		&__tip {
			position: relative;
			color: rgba($colorDarkText, 0.5);
			font-size: 11px;
			line-height: 1.455;
			text-transform: uppercase;
			b, strong {
				color: $colorDarkText;
				font-weight: 600;
			}
		}
		&__body {
			position: relative;
			width: 530px;
		}
		&__desc {
			position: relative;
			background: rgba($colorBg, 0.8);
			height: 316px;
			border-radius: 32px;
			padding: 36px 64px 36px 36px;
			overflow: hidden;
			&:not(:last-child) {
				margin-bottom: 82px;
			}
		}
		&__title {
			position: relative;
			font-size: 28px;
			line-height: 1.1;
			letter-spacing: -0.03em;
			opacity: 0;
			transform: translateY(50px);
			transition: opacity 0.5s, transform 0.5s;
			&:not(:last-child) {
				margin-bottom: 20px;
			}
		}
		&__text {
			position: relative;
			max-width: 364px;
			font-size: 16px;
			font-weight: 300;
			line-height: 1.48;
			letter-spacing: -0.01em;
			//opacity: 0.7;
			opacity: 0;
			transform: translateY(50px);
			transition: opacity 0.5s, transform 0.5s;
		}
		&__progress {
			position: relative;
			padding: 0 10px;
			z-index: 0;
			&:not(:last-child) {
				margin-bottom: 36px;
			}
			&-tip {
				position: absolute;
				background: rgba($colorBlue, 0.12);
				bottom: calc(100% + 12px);
				left: 17px;
				width: 52px;
				height: 32px;
				color: $colorBlue;
				font-size: 16px;
				font-weight: 600;
				line-height: 1.5;
				letter-spacing: -0.03em;
				text-align: center;
				border-radius: 8px;
				padding: 4px 8px;
				margin-left: 10px;
				transform: translateX(-50%);
				transition: left 0.5s;
				&:before {
					content: '';
					position: absolute;
					top: 100%;
					left: 50%;
					border-style: solid;
					border-width: 5px 4px 0 4px;
					border-color: rgba($colorBlue, 0.12) transparent transparent transparent;
					transform: translateX(-50%);
				}
			}
			&-line {
				position: absolute;
				background: linear-gradient(to right, rgba($colorLightText, 0.1) 0%, rgba($colorLightText, 1) 50%, rgba($colorLightText, 0.1) 100%);
				top: 17px;
				left: 0;
				width: 100%;
				height: 1px;
				opacity: 0.06;
			}
			&-bar {
				position: relative;
				display: flex;
				justify-content: space-between;
			}
			&-step {
				position: relative;
				display: flex;
				align-items: center;
				flex-direction: column;
				width: 80px;
				&.active {
					.partners-box__progress {
						&-point {
							&:after {
								background: $colorBlue;
							}
						}
					}
				}
				&.current {
					.partners-box__progress {
						&-point {
							border-width: 5px;
							&:before {
								opacity: 1;
							}
							&:after {
								background: $colorBlue;
								width: 9px;
								height: 9px;
								border-radius: 3px;
							}
						}
					}
				}
				&.step {
					&-left {
						align-items: flex-start;
						.partners-box__progress {
							&-title {
								border-radius: 0 8px 8px 8px;
							}
						}
					}
					&-center {
						align-items: center;
						.partners-box__progress {
							&-title {
								border-radius: 8px;
							}
						}
					}
					&-right {
						align-items: flex-end;
						.partners-box__progress {
							&-title {
								border-radius: 8px 0 8px 8px;
							}
						}
					}
				}
			}
			&-point {
				position: relative;
				background: $colorWhite;
				width: 35px;
				height: 35px;
				border-radius: 50%;
				border: 8px solid $colorLightBg;
				transition: border 0.5s;
				cursor: pointer;
				&:not(:last-child) {
					margin-bottom: 10px;
				}
				&:before {
					content: '';
					position: absolute;
					background: $colorWhite;
					top: 50%;
					left: 50%;
					width: 41px;
					height: 41px;
					border-radius: 50%;
					border: 1px solid $colorLightBg;
					transform: translate(-50%, -50%);
					transition: opacity 0.5s;
					z-index: -1;
					opacity: 0;
				}
				&:after {
					content: '';
					position: absolute;
					background: $colorDarkBorder;
					top: 50%;
					left: 50%;
					width: 7px;
					height: 7px;
					border-radius: 2px;
					transform: translate(-50%, -50%);
					transition: all 0.5s;
				}
			}
			&-title {
				position: relative;
				background: $colorLightBg;
				width: 100%;
				color: rgba($colorLightText, 0.3);
				font-size: 10px;
				line-height: 1.2;
				text-align: center;
				text-transform: uppercase;
				border-radius: 8px 0 8px 8px;
				padding: 5px 8px;
				b, strong {
					color: $colorBlue;
					font-weight: 600;
				}
			}
			&-img {
				&:not(:first-child) {
					margin-top: -36px;
				}
				&:not(:last-child) {
					margin-bottom: 32px;
				}
			}
		}
		&__buttons {
			position: relative;
			display: flex;
			padding: 0 10px;
			.btn {
				flex: 1 0 0;
				&:not(:last-child) {
					margin-right: 10px;
				}
			}
		}
	}
}
.advantages {
	position: relative;
	overflow: hidden;
	.subtitle {
		max-width: 380px;
	}
	&-inner {
		position: relative;
	}
	&-slider {
