			}
			&:not(:last-child) {
				margin-bottom: 60px;
			}
		}
	}
	&-box {
		position: relative;
		background-image: url('../img/advantage-bg-black.png');
		background-position: -94px -160px;
		background-repeat: no-repeat;
		height: 100%;
		border-radius: 36px;
		padding-top: 100%;
		overflow: hidden;
		&.bg {
			&-blue {
				background-image: url('../img/advantage-bg-cian.png');
				background-position: -252px -15px;
				.advantages-box {
					&__icon {
						background: rgba($colorBg, 0.3);
					}
					&__text {
						//opacity: 0.9;
					}
				}
			}
			&-gray {
				background-image: url('../img/advantage-bg-gray.png');
				background-position: 160px -290px;
			}
