		&__content {
			position: absolute;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			padding: 36px 58px 36px 36px;
		}
		&__head {
			position: relative;
		}
		&__icon {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			background: rgba($colorBg, 0.2);
			width: 62px;
			height: 62px;
			text-align: center;
			border-radius: 18px;
			img {
				max-height: 30px;
			}
		}
		&__body {
			position: relative;
		}
		&__title {
			position: relative;
			font-size: 28px;
			line-height: 1.1;
			letter-spacing: -0.03em;
			&:not(:last-child) {
				margin-bottom: 18px;
			}
