			position: relative;
			font-size: 16px;
			font-weight: 300;
			line-height: 1.48;
			letter-spacing: -0.01em;
			opacity: 0.7;
		}
	}
	&-button {
		position: relative;
		.btn {
			min-width: 230px;
		}
	}
	&-grid {
		position: relative;
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-gap: 24px;
		&:not(:first-child) {
			padding-top: 108px;
		}
		.advantages-box {
			&.bg {
				&-blue {
					.advantages-box {
						&__content {
							padding-right: 36px;
						}
					}
				}
				&-gray {
					.advantages-box {
						&__icon {
							background: rgba($colorText, 0.05);
						}
					}
				}
			}
		}
	}
}
.reviews {
	position: relative;
	&-inner {
		position: relative;
		display: flex;
		justify-content: space-between;
	}
	.subtitle {
		max-width: 310px;
	}
	&-content {
		position: relative;
		display: flex;
		flex-direction: column;
		width: 400px;
	}
	&-arrows {
		position: relative;
		display: flex;
		margin-top: auto;
		.slick {
			&-arrow {
				position: relative;
				top: auto;
				transform: none;
				&:not(:last-child) {
					margin-right: 10px;
				}
			}
			&-prev {
				left: auto;
			}
			&-next {
				right: auto;
			}
		}
	}
	&-slider {
		position: relative;
		//width: 460px;
		width: 506px;
		//padding-top: 22px;
		margin-top: -8px;
