		}
		&__list {
			position: relative;
			display: flex;
			align-items: center;
			&:hover {
				@media screen and (hover: hover) {
					.article-share__box {
						&:not(.js-article-share) {
							margin-left: 2px;
							opacity: 1;
						}
					}
				}
			}
			&.show {
				.article-share__box {
					&:not(.js-article-share) {
						margin-left: 2px;
						opacity: 1;
					}
					span {
						&:not(.icon) {
							&:before {
								left: 0;
							}
							&:after {
								right: 0;
							}
						}
					}
				}
			}
		}
		&__box {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			background: $colorWhite;
			width: 34px;
			height: 34px;
			color: $colorText;
			font-size: 16px;
			text-align: center;
			border-radius: 12px;
			transition: color 0.5s, margin 0.5s, opacity 0.5s;
			cursor: pointer;
			&.js-article-share{
				&:before {
					content: '';
					position: absolute;
					top: 2px;
					right: 2px;
					bottom: 2px;
					left: 2px;
					border: 1px solid $colorText;
					border-radius: 10px;
					opacity: 0.03;
				}
				&:hover {
					@media screen and (hover: hover) {
						color: $colorBlue;
						img {
							filter: invert(50%) sepia(76%) saturate(1339%) hue-rotate(195deg) brightness(100%) contrast(103%);
						}
					}
				}
			}
			&:not(.js-article-share) {
				margin-left: -34px;
				opacity: 0;
				&:hover {
					color: $colorBlue;
					img {
						filter: invert(50%) sepia(76%) saturate(1339%) hue-rotate(195deg) brightness(100%) contrast(103%);
					}
				}
			}
			&:nth-child(1) {
				z-index: 5;
			}
			&:nth-child(2) {
				z-index: 4;
			}
			&:nth-child(3) {
				z-index: 3;
			}
			&:nth-child(4) {
				z-index: 2;
			}
			&:nth-child(5) {
				z-index: 1;
			}
			&:hover {
				@media screen and (hover: hover) {
					span {
						&:not(.icon) {
							&:before {
								left: 0;
							}
							&:after {
								right: 0;
							}
						}
					}
				}
			}
			span {
				&:not(.icon) {
					position: absolute;
					background: $colorText;
					top: 50%;
					left: 50%;
					width: 2px;
					height: 2px;
					border-radius: 50%;
					transform: translate(-50%, -50%);
					&:before,
					&:after {
						content: '';
						position: absolute;
						background: $colorText;
						top: 0;
						width: 2px;
						height: 2px;
						border-radius: 50%;
					}
					&:before {
						left: -4px;
						transition: left 0.3s linear;
					}
					&:after {
						right: -4px;
						transition: right 0.3s linear;
					}
				}
			}
			img {
				max-width: 16px;
				transition: filter 0.1s;
			}
		}
	}
}
.content {
	position: relative;
	&-inner {
		position: relative;
	}
	&-title {
		position: relative;
		max-width: 652px;
		font-size: 54px;
		line-height: 0.9;
		letter-spacing: -0.03em;
		word-break: break-word;
		&:not(:last-child) {
			margin-bottom: 28px;
		}
		h1 {
			font-weight: 400;
		}
	}
	&-text {
		position: relative;
		max-width: 652px;
		color: rgba($colorWhite, 0.7);
		font-size: 16px;
		font-weight: 300;
		line-height: 1.48;
		letter-spacing: -0.01em;
		margin-bottom: 20px;
		&:not(:last-child) {
			margin-bottom: 86px;
		}
		p {
			&:not(:last-child) {
				margin-bottom: 24px;
			}
		}
		h1, h2, h3, h4, h5, h6 {
			color: $colorWhite;
			font-weight: 400;
			letter-spacing: -0.03em;
			&:not(:first-child) {
				margin-top: 56px;
			}
		}
		h1 {
			font-size: 84px;
			line-height: 0.9;
			&:not(:last-child) {
				margin-bottom: 48px;
			}
		}
		h2 {
			font-size: 72px;
			line-height: 1;
			&:not(:last-child) {
				margin-bottom: 36px;
			}
		}
		h3 {
			font-size: 54px;
			line-height: 1;
			&:not(:last-child) {
				margin-bottom: 36px;
			}
		}
		h4 {
			font-size: 40px;
			line-height: 1.1;
			&:not(:last-child) {
				margin-bottom: 32px;
			}
		}
		h5 {
			font-size: 28px;
			line-height: 1.1;
			&:not(:last-child) {
				margin-bottom: 28px;
			}
		}
		h6 {
			font-size: 20px;
			line-height: 1.24;
			&:not(:last-child) {
				margin-bottom: 24px;
			}
		}
		b, strong {
			color: $colorWhite;
			font-weight: 400;
		}
		a {
			color: $colorBlue;
			transition: color 0.5s;
			&:hover {
				color: $colorDarkBlue;
			}
		}
		ul {
			&:not(:first-child) {
				margin-top: 24px;
			}
			&:not(:last-child) {
				margin-bottom: 32px;
			}
			& > li {
				position: relative;
				padding-left: 30px;
				&:before {
					content: '';
					position: absolute;
					background: $colorWhite;
					top: 2px;
					left: 0;
					width: 19px;
					height: 19px;
					border-radius: 50%;
				}
				&:after {
					content: '';
					position: absolute;
					background: $colorBlue;
					top: 8px;
					left: 6px;
					width: 7px;
					height: 7px;
					border-radius: 2px;
				}
				&:not(:last-child) {
					margin-bottom: 10px;
				}
				ul, ol {
					margin-top: 10px;
					&:not(:last-child) {
						margin-bottom: 10px;
					}
				}
			}
		}
		ol {
			counter-reset: count;
			&:not(:first-child) {
				margin-top: 24px;
			}
			&:not(:last-child) {
				margin-bottom: 32px;
			}
			& > li {
				position: relative;
				padding-left: 30px;
				counter-increment: count;
				&:before {
					content: counter(count)'.';
					position: absolute;
					top: 0;
					left: 0;
					color: $colorBlue;
					font-weight: 600;
				}
				&:not(:last-child) {
					margin-bottom: 10px;
				}
				ol {
					counter-reset: count2;
					& > li {
						counter-increment: count2;
						&:before {
							content: counter(count)'.'counter(count2)'.';
						}
					}
				}
				ul, ol {
					margin-top: 10px;
					&:not(:last-child) {
						margin-bottom: 10px;
					}
				}
			}
		}
		table {
			width: 100%;
			border-collapse: collapse;
			&:not(:first-child) {
				margin-top: 24px;
			}
			thead {
				tr {
					td, th {
						color: $colorWhite;
						font-weight: 600;
						text-align: left;
					}
				}
			}
			tr {
				td, th {
					height: 48px;
					border: 1px solid $colorBorder;
					padding: 8px 16px;
				}
			}
		}
	}
}
.not-found {
	position: relative;
	display: flex;
	align-items: center;
	min-height: calc(100vh - 90px);
	&__inner {
		position: relative;
		margin-top: -90px;
	}
	&__box {
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: center;
		max-width: 460px;
		margin: 0 auto;
		.subtitle {
			max-width: 335px;
			font-size: 16px;
			&:not(:last-child) {
				margin-bottom: 40px;
			}
		}
		&-button {
			position: relative;
			.btn {
				min-width: 226px;
			}
		}
	}
}
