::placeholder {
	color: $colorWhite;
	font-weight: 400;
	opacity: 1;
}
input,
textarea {
	position: relative;
	background: rgba($colorBg, 0.1);
	color: $colorWhite;
	width: 100%;
	font-size: 11px;
	font-weight: 400;
	font-family: 'Averta CY', sans-serif;
	text-transform: uppercase;
  	border: 0;
	border-radius: 32px;
	padding: 0 20px;
	appearance: none;
	&:hover {
		border-color: $colorDarkGrey;
	}
	&:active,
	&:focus {
		&:not(.btn) {
			&:not([disabled]) {
				&::-webkit-input-placeholder,
				&::-moz-placeholder,
				&:-ms-input-placeholder,
				&:-moz-placeholder {
					color: transparent;
				}
			}
		}
	}
	&[disabled],
	&.disabled{
		opacity: 0.5;
	}
}
input {
	height: 56px;
	text-overflow: ellipsis;
	overflow: hidden;
}
textarea {
	vertical-align: top;
	height: 158px;
	padding: 20px;
	resize: none;
}
.has {
	&-error {
		.form {
			&-group {
				label {
					color: $colorRed !important;
				}
			}
		}
		::placeholder {
			color: $colorRed !important;
		}
		input,
		textarea {
			border-color: $colorRed !important;
		}
	}
	&-success {
		input,
		textarea {
			//border-color: $colorGreen !important;
		}
	}
}
label {
	display: block;
	margin-bottom: 0;
}
.form{
	&-title {
		position: relative;
		font-size: 28px;
		line-height: 1.1;
		letter-spacing: -0.03em;
		&:not(:last-child) {
			margin-bottom: 24px;
		}
	}
	&-body {
		position: relative;
	}
	&-text {
		position: relative;
		&:not(:last-child) {
			margin-bottom: 20px;
		}
	}
	&-group {
		position: relative;
		&:not(:last-child) {
			margin-bottom: 4px;
		}
		label {
			position: absolute;
			top: 21px;
			left: 20px;
			font-size: 11px;
			font-weight: 400;
			line-height: 1.273;
			text-transform: uppercase;
			pointer-events: none;
			z-index: 1;
		}
		textarea {
			& ~ .error-text {
				top: 19px;
				left: 120px;
			}
		}
	}
	&-flex {
		display: flex;
		&:not(:last-child) {
			margin-bottom: 20px;
		}
		.form {
			&-group {
				&:not(:last-child) {
					margin-right: 20px;
					margin-bottom: 0;
				}
			}
		}
	}
	&-button {
		position: relative;
		&:not(:last-child) {
			margin-bottom: 20px;
		}
	}
	&-checkbox {
		position: relative;
		.error-text {
			position: relative;
			top: auto;
			left: auto;
		}
	}
}
.error-text {
	position: absolute;
	top: 21px;
	left: 100px;
	font-size: 11px;
	line-height: 1.273;
	text-transform: uppercase;
	opacity: 0.3;
}
.checkbox {
	position: relative;
	display: inline-block;
	[type="checkbox"],
	[type="radio"] {
		position: absolute;
		left: -9999px;
		width: inherit;
		height: inherit;
		& ~ label {
			position: relative;
			display: inline-block;
			min-height: 16px;
			color: rgba($colorWhite, 0.4);
			font-size: 12px;
			font-weight: 300;
			line-height: 16px;
			padding: {
				top: 10px;
				bottom: 10px;
				left: 16px;
			};
			cursor: pointer;
			&:before {
				content: '';
				position: absolute;
				background: rgba($colorBg, 0.1);
				top: 10px;
				left: 0;
				width: 16px;
				height: 16px;
				transition: background 0.5s, border-color 0.5s;
			}
			&:after {
				content: '';
				position: absolute;
				transition: opacity 0.5s;
				opacity: 0;
			}
			&:hover {
				&:before {
					//border-color: $colorDarkGrey;
				}
			}
			&:not(:empty) {
				padding-left: 28px;
			}
			a {
				position: relative;
				color: rgba($colorWhite, 0.4);
				background-image: linear-gradient(to right, rgba($colorWhite, 0.4), rgba($colorWhite, 0.4)), linear-gradient(to right, rgba($colorBlue, 0.4), rgba($colorBlue, 0.4));
				background-size: 100% 1px, 0 1px;
				background-position: 100% 100%, 0 100%;
				background-repeat: no-repeat;
				transition: background-size 0.6s;
				//&:before,
				//&:after {
				//	content: '';
				//	position: absolute;
				//	bottom: -2px;
				//	height: 1px;
				//	opacity: 0.3;
				//	transition: width 0.5s;
				//	pointer-events: none;
				//}
				//&:before {
				//	background: $colorWhite;
				//	width: 100%;
				//	right: 0;
				//}
				//&:after {
				//	background: $colorBlue;
				//	left: 0;
				//	width: 0;
				//}
				&:hover {
					//&:before {
					//	width: 0;
					//}
					//&:after {
					//	width: 100%;
					//}
					background-size: 0 1px, 100% 1px;
				}
			}
		}
		&:disabled {
			& ~ label {
				&:before {
					opacity: 0.5;
				}
			}
		}
		&:checked {
			& ~ label {
				&:before {

				}
				&:after {
					opacity: 1;
				}
			}
			&:disabled {
				& ~ label {
					&:before {
						opacity: 0.5;
					}
					&:after {
						opacity: 0.5;
					}
				}
			}
		}
	}
	[type="checkbox"] {
		& ~ label {
			&:before{
				border-radius: 4px;
			}
			&:after {
				content: '\EA09';
				display: flex;
				align-items: center;
				justify-content: center;
				background: $colorText;
				top: 11px;
				left: 1px;
				width: 14px;
				height: 14px;
				color: $colorBlue;
				font-size: 14px;
				font-family: 'svgfont', sans-serif;
				border-radius: 3px;
			}
		}
	}
	[type="radio"] {
		& ~ label {
			&:before {
				background: $colorGray;
				top: 4px;
				left: 4px;
				width: 12px;
				height: 12px;
				border-radius: 50%;
			}
			&:after {
				border-radius: 50%;
			}
		}
	}
	&.has {
		&-error {
			[type="checkbox"],
			[type="radio"] {
				& ~ label {
					&:before {
						border-color: $colorRed !important;
					}
				}
			}
		}
	}
}
.help-block {
	position: relative;
	color: $colorRed;
	font-size: 12px;
	line-height: 16px;
	padding: 5px 20px;
}
