$colorBlack: #000;
$colorWhite: #FFF;
$colorGray: #666;
$colorLightGrey: #999;
$colorDarkGrey: #333;
$colorGreen: #00CC00;
$colorLightGreen: lighten($colorGreen, 10);
$colorDarkGreen: darken($colorGreen, 10);
$colorRed: #FF4337;
$colorLightRed: lighten($colorRed, 10);
$colorDarkRed: darken($colorRed, 10);

$colorBg: #EFF4F7;
$colorLightBg: #F2F6F8;
$colorLightenBg: #ECEFF1;
$colorDarkBg: #F8F9FB;
$colorDarkenBg: #EFF2F5;
$colorBorder: #E0E3E6;
$colorLightBorder: #E7E7EB;
$colorDarkBorder: #E5E6E9;
$colorText: #222836;
$colorLightText: #2A2C3A;
$colorLightenText: #3C3E4F;
$colorDarkText: #242631;
$colorDarkenText: #2B2D3A;
$colorModal: #383D4A;
$colorDarkModal: #1C212F;
$colorBlue: #5597FF;
$colorDarkBlue: #3479E7;
$colorCian: #53BDB0;
//$colorCian: #15C87F;
//$colorLightCian: #19C07C;
//$colorDarkCian: #0EBD76;

html,
body {
	height: 100%;
	scrollbar-color: rgba($colorWhite, 0.5) rgba($colorText, 0.8);
	scrollbar-width: 8px;
	scroll-behavior: smooth
}
body {
	background: $colorText;
	color: $colorWhite;
	font-size: 14px;
	font-family: 'Averta CY', sans-serif;
	line-height: 1.25;
}
::-webkit-scrollbar {
	width: 8px;
}
::-webkit-scrollbar-track {
	background-color: rgba($colorText, 0.8);
	border-radius: 4px;
}
::-webkit-scrollbar-thumb {
	background-color: rgba($colorWhite, 0.5);
	border-radius: 4px;
}
* {
	outline: none;
	box-sizing: border-box;
	&:before,
	&:after {
		box-sizing: border-box;
	}
}
h1, h2, h3, h4, h5, h6 {
	font-size: 1em;
	line-height: 1;
	margin: 0;
}
img {
	vertical-align: top;
	max-width: 100%;
}
a {
	text-decoration: none;
	&:hover{
		text-decoration: none;
	}
}
ul,
ol {
	list-style-type: none;
	list-style-position: inside;
	padding: 0;
	margin: 0;
}
p {
	margin: 0;
}
button {
	outline: none;
	&:focus{
		outline: none;
	}
}
[class^='icon-'],
[class*=' icon-'] {
	position: relative;
	vertical-align: middle;
}
.wrapper {
	position: relative;
	display: flex;
	flex-direction: column;
	//width: 100vw;
	//max-height: 100vh;
	min-width: 375px;
	min-height: 100%;
	overflow: hidden;
	&[data-scrollbar] {
		display: flex;
	}
	& > .scroll-content {
		display: flex;
		flex-direction: column;
		flex: 1 0 0;
		max-width: 100vw;
	}
}
.container {
	//max-width: 1136px;
	max-width: 1184px;
	padding: {
		left: 24px;
		right: 24px;
	};
	margin: {
		left: auto;
		right: auto;
	};
}
.section {
	position: relative;
	padding: 108px 0;
	&-group {
		position: relative;
		padding: 108px 0;
		margin: 0 24px;
		overflow: hidden;
		&:last-child {
			margin-bottom: 102px;
		}
		&.bg {
			&-gray {
				border-radius: 40px;
			}
		}
		.section {
			padding: 0;
			&:not(:last-child) {
				margin-bottom: 108px;
			}
		}
	}
}
.title {
	position: relative;
	&:not(:last-child) {
		margin-bottom: 28px;
	}
	h1 {
		font-size: 84px;
		font-weight: 400;
		line-height: 0.9;
		letter-spacing: -0.03em;
		b, strong {
			color: $colorBlue;
			font-weight: 400;
		}
	}
	h2 {
		font-size: 72px;
		font-weight: 400;
		line-height: 1;
		letter-spacing: -0.03em;
		word-break: break-word;
	}
}
.subtitle {
	position: relative;
	color: rgba($colorWhite, 0.7);
	font-size: 18px;
	font-weight: 300;
	line-height: 1.5;
	letter-spacing: -0.01em;
	//opacity: 0.7;
	&.text-center {
		margin: 0 auto;
	}
}
.text {
	&-left {
		text-align: left !important;
	}
	&-center {
		text-align: center !important;
	}
	&-right {
		text-align: right !important;
	}
}
.link {
	&-download {
		color: $colorWhite;
		font-size: 11px;
		line-height: 1.273;
		text-transform: uppercase;
		opacity: 0.6;
		&:hover {
			.circle {
				&-shadow {
					stroke-opacity: 0.3;
				}
				&-line {
					animation: circleLine 0.5s cubic-bezier(0.645, 0.045, 0.355, 1.000) forwards;
				}
			}
		}
		.circle {
			position: absolute;
			width: 16px;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%) rotate(30deg);
			stroke: $colorWhite;
			stroke-width: 1px;
			stroke-dasharray: 44;
			transition: opacity 0s;
			pointer-events: none;
			&-shadow {
				stroke-dashoffset: 0;
				opacity: 0.12;
			}
			&-line {
				stroke-dashoffset: 44;
			}
		}
		.icon {
			font-size: 16px;
			margin-top: -3px;
			margin-left: 8px;
		}
	}
}
.bg {
	&-black {
		background-color: $colorText !important;
		color: $colorWhite;
	}
	&-blue {
		background-color: $colorBlue !important;
		color: $colorWhite;
	}
	&-cian {
		background-color: $colorCian !important;
		color: $colorWhite;
	}
	&-gray {
		background-color: $colorBg !important;
		color: $colorText;
		.subtitle {
			color: rgba($colorText, 0.7);
		}
		.link {
			&-download {
				color: $colorText;
				.circle {
					stroke: $colorText;
				}
			}
		}
		.checkbox {
			[type="checkbox"],
			[type="radio"] {
				& ~ label {
					color: rgba($colorText, 0.4);
					a {
						color: rgba($colorText, 0.4);
						&:hover {
							color: $colorText;
						}
					}
				}
			}
		}
		.pagination {
			ul {
				li {
					&.page {
						&-prev,
						&-next {
							a {
								background: rgba($colorText, 0.06);
								color: $colorText;
								&:hover {
									background: rgba($colorText, 0.1);
								}
								&.disabled,
								[disabled] {
									background: rgba($colorText, 0.04);
									color: rgba($colorText, 0.3);
								}
							}
						}
					}
					a {
						color: rgba($colorText, 0.4);
						&:hover,
						&.active {
							color: $colorText;
						}
					}
				}
			}
		}
		.article {
			&-date {
				color: rgba($colorText, 0.4);
			}
			&-text {
				color: rgba($colorText, 0.7);
				h1, h2, h3, h4, h5, h6 {
					color: $colorText;
				}
				table {
					thead {
						tr {
							td, th {
								color: $colorText;
							}
						}
					}
				}
			}
		}
		.content {
			&-text {
				color: rgba($colorText, 0.7);
				h1, h2, h3, h4, h5, h6 {
					color: $colorText;
				}
				b, strong {
					color: $colorText;
				}
				table {
					thead {
						tr {
							td, th {
								color: $colorText;
							}
						}
					}
				}
			}
		}
	}
}
.pagination	{
	position: relative;
	text-align: center;
	ul {
		position: relative;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		li {
			position: relative;
			margin: 0 4px;
			&.page {
				&-prev,
				&-next {
					a {
						background: rgba($colorWhite, 0.1);
						width: 48px;
						height: 48px;
						color: $colorWhite;
						&:hover {
							background: rgba($colorWhite, 0.18);
						}
						&.disabled,
						[disabled] {
							background: rgba($colorWhite, 0.04);
							color: rgba($colorWhite, 0.3);
						}
					}
				}
				&-prev {
					&:not(:last-child) {
						margin-right: 20px;
					}
				}
				&-next {
					&:not(:first-child) {
						margin-left: 20px;
					}
				}
			}
			a {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				width: 20px;
				height: 20px;
				color: rgba($colorWhite, 0.4);
				font-size: 13px;
				font-weight: 300;
				line-height: 1.385;
				border-radius: 50%;
				transition: color 0.5s, background 0.5s;
				&:hover,
				&.active {
					color: $colorWhite;
				}
				&.active {
					pointer-events: none;
				}
				&.disabled,
				[disabled] {
					pointer-events: none;
				}
			}
		}
	}
}
.slick {
	&-arrow {
		position: absolute;
		background: rgba($colorWhite, 0.1);
		top: 50%;
		width: 56px;
		height: 56px;
		font-size: 0;
		border: 0;
		border-radius: 50%;
		padding: 0;
		transform: translateY(-50%);
		transition: background 0.5s;
		cursor: pointer;
		z-index: 10;
		&:before {
			content: '';
			display: flex;
			align-items: center;
			justify-content: center;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			color: $colorWhite;
			font-size: 18px;
			font-family: 'svgfont', sans-serif;
		}
		&:hover {
			@media screen and (hover: hover) {
				background: rgba($colorWhite, 0.18);
			}
		}
	}
	&-prev {
		left: 0;
		&:before {
			content: '\EA02';
		}
	}
	&-next {
		right: 0;
		&:before {
			content: '\EA03';
		}
	}
	&-disabled {
		background: rgba($colorWhite, 0.04);
		pointer-events: none;
		&:before {
			opacity: 0.3;
		}
	}
	&-dots {
		position: absolute;
		display: flex;
		align-items: center;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
		li {
			font-size: 0;
			margin: 0 4px;
			&.slick-active {
				button {
					background: $colorBlue;
					transform: scale(1.3325);
					&:before {
						content: '';
						position: absolute;
						top: -2px;
						right: -2px;
						bottom: -2px;
						left: -2px;
						border-radius: 50%;
						border: 1px solid $colorBlue;
						opacity: 0.3;
					}
				}
			}
			button {
				background: $colorLightBorder;
				width: 6px;
				height: 6px;
				border: 0;
				border-radius: 50%;
				padding: 0;
			}
		}
	}
}
.hide {
	display: none !important;
}

.modal {
	position: fixed;
	background: $colorText;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	padding: 24px;
	opacity: 0;
	visibility: hidden;
	overflow-y: auto;
	transition: opacity 0.5s;
	z-index: 100;
	&.show {
		opacity: 1;
		visibility: visible;
	}
	&-wrapper {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		background: $colorDarkModal;
		min-height: calc(100vh - 48px);
		border-radius: 40px;
		padding: 36px 24px;
		overflow: hidden;
	}
	&-bg {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-position: center;
			object-fit: none;
		}
	}
	&-box {
		position: relative;
		width: 870px;
	}
	&-title {
		position: relative;
		font-size: 54px;
		line-height: 0.9;
		letter-spacing: -0.03em;
		text-align: center;
		&:not(:last-child) {
			margin-bottom: 60px;
		}
	}
	&-partners {
		position: relative;
		display: flex;
		&__box {
			position: relative;
			background-image: url('../img/advantage-bg-cian.png');
			background-position: -312px -62px;
			display: flex;
			flex-direction: column;
			flex: 1 0 0;
			border-radius: 40px;
			padding: 36px;
			margin: 0 12px;
			&:hover {
				.modal-partners__box {
					&-button {
						color: $colorBlue;
						&:before {
							transform: translate(-50%, -50%) scale(1.01);
						}
					}
				}
			}
			&-icon {
				position: relative;
				background: rgba($colorWhite, 0.3);
				display: flex;
				align-items: center;
				justify-content: center;
				width: 62px;
				height: 62px;
				border-radius: 18px;
				&:not(:last-child) {
					margin-bottom: 20px;
				}
				img {
					max-width: 20px;
					max-height: 24px;
					filter: grayscale(1) brightness(5);
				}
			}
			&-title {
				position: relative;
				font-size: 28px;
				line-height: 1.1;
				letter-spacing: -0.03em;
				&:not(:last-child) {
					margin-bottom: 18px;
				}
			}
			&-text {
				position: relative;
				font-size: 14px;
				font-weight: 300;
				line-height: 1.4285;
				letter-spacing: -0.02em;
				opacity: 0.9;
				&:not(:last-child) {
					margin-bottom: 28px;
				}
			}
			&-button {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				width: 52px;
				height: 52px;
				font-size: 16px;
				border: 1px solid rgba($colorWhite, 0.4);
				border-radius: 50%;
				margin-top: auto;
				transition: color 0.5s;
				will-change: transform;
				overflow: hidden;
				&:before {
					content: '';
					position: absolute;
					background: $colorWhite;
					top: 50%;
					left: 50%;
					width: 100%;
					height: 100%;
					border-radius: 50%;
					transform: translate(-50%, -50%) scale(0);
					transition: transform 0.5s;
				}
			}
		}
	}
	&-close {
		position: absolute;
		top: 24px;
		right: 24px;
		font-size: 20px;
		line-height: 0;
		cursor: pointer;
		&:hover {
			.icon {
				transition: transform 0.5s;
				transform: rotate(180deg);
			}
		}
	}
}

@keyframes arcLine {
	from {
		stroke-dashoffset: 157;
	}
	to {
		stroke-dashoffset: 76;
	}
}

@keyframes circleLine {
	from {
		stroke-dashoffset: 44;
	}
	to {
		stroke-dashoffset: 0;
	}
}
