.btn {
	position: relative;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	background: $colorBlue;
	height: 56px;
	color: $colorWhite;
	font-size: 16px;
	font-weight: 400;
	font-family: 'Averta CY', sans-serif;
	line-height: 1.25;
	letter-spacing: -0.01em;
	text-align: center;
	text-decoration: none;
	border: 0;
	border-radius: 40px;
	box-shadow: none;
	padding: 8px 24px;
	will-change: transform;
	transition: color 0.5s;
	outline: none;
	cursor: pointer;
	overflow: hidden;
	&:hover,
	&:active {
		color: $colorWhite;
		text-decoration: none;
		outline: none;
	}
	&:focus {
		box-shadow: none;
		outline: none;
	}
	&-blue {
		background: $colorBlue;
		color: $colorWhite;
		&:before {
			content: '';
			position: absolute;
			background: $colorDarkBlue;
			top: 50%;
			left: 50%;
			width: 100%;
			border-radius: 50%;
			padding-top: 100%;
			transition: none;
			transform: translate(-50%, -50%) scale(0);
		}
		&:hover,
		&:active {
			color: $colorWhite;
			&:before {
				transition: transform 0.5s cubic-bezier(0.1, 0, 0.3, 1);
				transform: translate(-50%, -50%) scale(1);
			}
		}
	}
	&-white {
		background: $colorWhite;
		color: $colorText;
		&:before {
			content: '';
			position: absolute;
			background: $colorDarkBlue;
			top: 50%;
