.header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	height: 90px;
	padding: 24px 0;
	//transition: background 0.3s;
	z-index: 50;
	&.fixed {
		background: $colorText;
	}
	&.show-menu {
		transform: none !important;
	}
	&-inner {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	&-modal {
		position: relative;
		&__bg {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			pointer-events: none;
			img {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				object-position: center top;
				object-fit: none;
			}
		}
	}
	&-right {
		position: relative;
		display: flex;
		align-items: center;
	}
	&-account {
		position: relative;
		&:hover {
			.btn {
				.icon {
					transform: rotateX(180deg);
				}
			}
			.header-account__modal {
				transition: opacity 0.5s;
				visibility: visible;
				opacity: 1;
			}
		}
		.btn {
			min-width: 110px;
			&:before {
				content: none;
			}
			.icon {
				font-size: 14px;
				margin: {
					left: 4px;
					right: -4px;
				};
				cursor: pointer;
				opacity: 0.4;
			}
		}
		&__modal {
			position: absolute;
			top: 100%;
			left: 0;
			min-width: 100%;
			padding-top: 5px;
			visibility: hidden;
			opacity: 0;
			ul {
				position: relative;
				background: $colorModal;
				border-radius: 18px;
				padding: 7px 0;
				li {
					display: block;
					padding: 0;
					&:not(:last-child) {
						margin-right: 0;
					}
					a {
						display: block;
						color: $colorWhite;
						padding: 7px 14px;
						transition: opacity 0.5s;
						opacity: 0.5;
						&:hover {
							color: $colorWhite;
							opacity: 1;
						}
					}
				}
			}
		}
	}
	&-button {
		position: relative;
		display: none;
	}
}
.logo {
	position: relative;
	img {
		max-height: 43px;
	}
}
.menu-toggle {
	position: relative;
	display: none;
	width: 32px;
	height: 32px;
	will-change: transform;
	cursor: pointer;
	&:before,
	&:after {
		content: '';
		position: absolute;
		background: $colorWhite;
		top: 50%;
		left: 0;
		width: 100%;
		height: 1.2px;
		border-radius: 3px;
		margin-top: -0.6px;
		transition: margin 0.2s linear 0.3s, transform 0.2s linear 0s;
	}
	&:before {
		margin-top: -3.5px;
	}
	&:after {
		margin-top: 2.3px;
	}
	&.active {
		&:before,
		&:after {
			margin-top: -0.6px;
			transition: margin 0.2s linear 0s, transform 0.2s linear 0.3s;
		}
		&:before {
			transform: rotate(45deg);
		}
		&:after {
			transform: rotate(-45deg);
		}
	}
}
.top-menu {
	position: relative;
	ul {
		display: flex;
		align-items: center;
		li {
			position: relative;
			display: inline-flex;
			align-items: center;
			padding: 10px 0;
			&:hover {
				ul {
					transition: opacity 0.5s;
					visibility: visible;
					opacity: 1;
				}
				& > .icon {
					transform: rotateX(180deg);
				}
			}
			&:not(:last-child) {
				margin-right: 44px;
			}
			a,
			span:not(.icon) {
				color: $colorWhite;
				font-size: 15px;
				line-height: 1.33334;
				transition: color 0.5s;
				cursor: pointer;
			}
			a {
				&:hover {
					color: $colorBlue;
				}
				&.active {
					color: $colorBlue;
					pointer-events: none;
				}
			}
			.icon {
				font-size: 14px;
				margin-left: 4px;
				cursor: pointer;
				opacity: 0.4;
			}
			ul {
				position: absolute;
				background: $colorModal;
				display: block;
				top: 100%;
				left: 0;
				min-width: 140px;
				border-radius: 18px;
				padding: 7px 0;
				visibility: hidden;
				opacity: 0;
				li {
					display: block;
					padding: 0;
					&:not(:last-child) {
						margin-right: 0;
					}
					a {
						display: block;
						color: $colorWhite;
						padding: 7px 14px;
						transition: opacity 0.5s;
						opacity: 0.5;
						&:hover {
							color: $colorWhite;
							opacity: 1;
						}
					}
				}
			}
		}
	}
}
.lang {
	position: relative;
	&:hover {
		.lang {
			&-title {
				.icon {
					transform: rotateX(180deg);
				}
			}
			&-modal {
				transition: opacity 0.5s;
				visibility: visible;
				opacity: 1;
			}
		}
	}
	&:not(:last-child) {
		margin-right: 36px;
	}
	&-title {
		position: relative;
		color: $colorWhite;
		font-size: 15px;
		line-height: 1.33334;
		padding: {
			top: 10px;
			right: 18px;
			bottom: 10px;
		};
		cursor: pointer;
		.icon {
			position: absolute;
			top: 50%;
			right: 0;
			font-size: 14px;
			margin-top: -7px;
			opacity: 0.4;
		}
	}
	&-modal {
		position: absolute;
		background: $colorModal;
		top: 100%;
		left: 50%;
		width: 84px;
		min-width: 100%;
		border-radius: 8px;
		transform: translateX(-50%);
		overflow: hidden;
		visibility: hidden;
		opacity: 0;
		ul {
			display: flex;
			flex-wrap: wrap;
			li {
				position: relative;
				width: 50%;
				//border-bottom: 1px solid $colorBorder;
				a {
					display: block;
					color: $colorWhite;
					font-size: 15px;
					line-height: 1.33334;
					text-align: center;
					padding: 10px 8px;
					opacity: 0.5;
					transition: opacity 0.5s;
					&:hover {
						color: $colorWhite;
						opacity: 1;
					}
					&.active {
						opacity: 1;
						pointer-events: none;
					}
				}
			}
		}
	}
}
.lang-mobile {
	position: relative;
	display: none;
}
