@media screen and (min-width: 768px) {
	.partners {
		&-box {
			display: none;
			&.show {
				display: block;
			}
		}
	}
}

@media screen and (min-width: 992px) {
	.partners {
		&-box {
			&.show {
				display: flex;
			}
		}
	}
}

@media screen and (max-width: 1199px) {
	.section {
		padding: 80px 0;
		&-group {
			padding: 80px 0;
			margin: 0 16px;
			&:last-child {
				margin-bottom: 80px;
			}
			.section {
				&:not(:last-child) {
					margin-bottom: 80px;
				}
			}
		}
	}
	.title {
		h1 {
			font-size: 64px;
		}
		h2 {
			font-size: 56px;
		}
	}
	.btn {
		padding: 8px 16px;
	}
	.modal {
		padding: 16px;
		&-wrapper {
			min-height: calc(100vh - 32px);
		}
		&-box {
			width: 810px;
		}
		&-title {
			font-size: 44px;
			&:not(:last-child) {
				margin-bottom: 48px;
			}
		}
		&-partners {
			&__box {
				padding: 32px;
				margin: 0 8px;
			}
		}
	}
	.header {
		&-modal {
			position: fixed;
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			background-color: $colorText;
			top: 90px;
			left: 100vw;
			bottom: 0;
			//height: calc(100vh - 90px);
			width: 100vw;
			padding: 28px 24px;
			transition: left 0.5s;
			will-change: transform;
			overflow-x: hidden;
			overflow-y: auto;
			&.show {
				left: 0;
				.header-modal {
					&__bg {
						left: 0;
					}
				}
			}
			&__bg {
				top: -90px;
				left: -100vw;
				transition: left 0.5s;
				//background-position: center -88px;
				//background-repeat: no-repeat;
			}
		}
		&-account {
			display: none;
		}
		&-button {
			display: block;
			.btn {
				min-width: 320px;
			}
		}
	}
	.menu-toggle {
		display: block;
	}
	.top-menu {
		width: 100%;
		&:not(:last-child) {
			margin-bottom: 24px;
		}
		ul {
			display: block;
			li {
				flex-wrap: wrap;
				width: 100%;
				padding: 0;
				&:not(:last-child) {
					margin-right: 0;
					margin-bottom: 24px;
				}
				&:hover {
					& > .icon {
						transform: none;
					}
				}
				&.show {
					& > .icon {
						transform: rotateX(180deg);
					}
				}
				a,
				span:not(.icon) {
					font-size: 24px;
					line-height: 1.25;
				}
				.icon {
					font-size: 24px;
					margin-left: 4px;
				}
				ul {
					position: relative;
					background: transparent;
					display: none;
					top: auto;
					left: auto;
					width: 100%;
					padding: {
						top: 20px;
						left: 24px;
					};
					visibility: visible;
					opacity: 1;
					li {
						display: flex;
						&:not(:last-child) {
							border-bottom: 0;
						}
						a {
							color: $colorWhite;
							padding: 0;
							transition: color 0.5s;
							opacity: 1;
							&:hover {
								color: $colorBlue;
							}
						}
					}
				}
			}
		}
	}
	.lang {
		display: none;
		margin-top: auto;
		&:not(:last-child) {
			margin-bottom: 52px;
		}
		&:hover {
			.lang {
				&-title {
					.icon {
						transform: none;
					}
				}
				&-modal {
					visibility: hidden;
					opacity: 0;
				}
			}
		}
		&.show {
			.lang {
				&-title {
					.icon {
						transform: rotateX(180deg);
					}
				}
				&-modal {
					visibility: visible;
					opacity: 1;
				}
			}
		}
		&-title {
			font-size: 24px;
			padding: 0 28px 0 0;
			.icon {
				font-size: 24px;
				margin-top: -12px;
			}
		}
		&-modal {
			top: auto;
			bottom: 100%;
			left: 0;
			transform: none;
			width: 100px;
			margin-bottom: 8px;
			ul {
				li {
					a {
						font-size: 24px;
					}
				}
			}
		}
	}
	.lang-mobile {
		display: block;
		margin-top: auto;
		&:not(:last-child) {
			margin-bottom: 52px;
		}
		select {
			background: transparent;
			width: 56px;
			height: 32px;
			color: #FFF;
			font-size: 24px;
			line-height: 1.33334;
			border: 0;
			cursor: pointer;
			-webkit-appearance: none;
			-moz-appearance: none;
		}
		option {
			color: $colorText;
		}
		.icon {
			position: absolute;
			top: 50%;
			right: 0;
			font-size: 24px;
			margin-top: -12px;
			opacity: 0.4;
			pointer-events: none;
		}
	}
	@media screen and (hover: hover) {
		.lang {
			display: block;
		}
		.lang-mobile {
			display: none;
		}
	}
	.first {
		padding-top: 56px;
		&-content {
			width: 420px;
		}
		&-link {
			padding-top: 48px;
			a {
				height: 54px;
				min-width: 136px;
				img {
					max-height: 24px;
				}
			}
		}
	}
	.partners {
		&-group {
			padding-top: 36px;
		}
		&-tabs {
			&__box {
				min-width: 168px;
			}
		}
		&-box {
			&__map {
				width: 1100px;
			}
 			&__head {
				width: 350px;
			}
			&__info {
				font-size: 38px;
			}
			&__body {
				width: 460px;
			}
			&__progress {
				&-step {
					width: 72px;
				}
			}
		}
	}
	.advantages {
		&-slider {
			max-width: 780px;
			margin: 0 auto;
			&:not(:last-child) {
				margin-bottom: 52px;
			}
			&:not(:first-child) {
				margin-top: 52px;
			}
			.slick {
				&-slide {
					padding: 0 8px;
				}
			}
			.owl {
				&-item {
					padding: 0 8px;
				}
			}
		}
		&-box {
			&__content {
				padding: 32px 48px 32px 32px;
			}
		}
		&-grid {
			grid-template-columns: repeat(2, 1fr);
			&:not(:first-child) {
				padding-top: 80px;
			}
		}
	}
	.reviews {
		&-content {
			width: 350px;
		}
		&-slider {
			width: 480px;
		}
		&-card {
			&__box {
				padding: 30px 30px 14px 30px;
			}
		}
	}
	.faq {
		&-content {
			max-width: 800px;
		}
	}
	.contacts {
		&-group {
			padding-top: 24px;
		}
		&-content {
			width: 500px;
		}
		&-team {
			&__box {
				&-head {
					width: 84px;
					height: 84px;
					&:not(:last-child) {
						margin-right: 16px;
					}
				}
				&-img {
					width: 84px;
					height: 82px;
				}
			}
		}
		&-info {
			&__box {
				padding: 30px;
				&-date{
					top: 30px;
					right: 30px;
				}
			}
		}
	}
	.footer {
		padding-bottom: 64px;
		&-top {
			&:not(:last-child) {
				margin-bottom: 80px;
			}
		}
		&-menu {
			width: 440px;
			&:not(:first-child) {
				margin-left: 100px;
			}
		}
	}
	.partner {
		margin: -56px 0;
		&-body {
			width: 400px;
			padding: 36px 0;
		}
		&-icon {
			position: relative;
			top: auto;
			left: auto;
			&:not(:last-child) {
				margin-bottom: 16px;
			}
		}
		&-heading {
			font-size: 56px;
			&:not(:last-child) {
				margin-bottom: 48px;
			}
		}
		&-head {
			width: calc(50vw - 16px - 24px - 12px);
			padding: 32px;
			margin-right: 0;
			img {
				height: 460px;
			}
		}
	}
	.about {
		&-content {
			background-position: -96px 260px;
			padding: 32px;
		}
		&-list {
			padding-top: 52px;
		}
		&-tabs {
			&__box {
				&-title {
					padding: 20px 32px;
				}
				&-text {
					padding: 0 32px;
				}
			}
		}
	}
	.trust {
		&-slider {
			width: 320px;
			&:before {
				left: -20px;
			}
			&:after {
				right: -20px;
			}
			&__bg {
				width: 308px;
			}
		}
		&-box {
			height: 176px;
			&__title {
				font-size: 76px;
				transform: translateY(15px);
			}
			&__text {
				&:not(:first-child) {
					margin-top: 8px;
				}
			}
		}
	}
	.revenue {
		&-box {
			&:not(:first-child) {
				margin-top: 48px;
			}
			&__head {
				width: 320px;
				padding: 6px;
			}
			&__title {
				font-size: 36px;
			}
			&__body {
				height: 254px;
				width: 56%;
				padding: 32px;
			}
		}
	}
	.benefits {
		&-box {
			&__text {
				padding-top: 8px;
			}
		}
	}
	.news {
		padding-top: 48px;
		&:not(:last-child) {
			margin-bottom: 0;
		}
		&-catalog {
			margin: {
				top: -16px;
				bottom: -8px;
			};
		}
		&-box {
			height: 272px;
			&.news-big {
				height: 534px;
			}
			&__head {
				&:not(:last-child) {
					margin-right: 0;
				}
			}
			&__info {
				&:not(:last-child) {
					margin-bottom: 16px;
				}
			}
			&__content {
				max-height: 178px;
			}
		}
		&-other {
			.news-list {
				padding-top: 24px;
			}
		}
	}
	.article {
		margin-top: -16px;
		&-title {
			max-width: 486px;
			font-size: 44px;
		}
		&-img,
		&-description,
		&-gallery,
		&-text,
		&-blockquote {
			&:not(:last-child) {
				margin-bottom: 68px;
			}
		}
		&-img {
			height: 420px;
		}
		&-text {
			h1 {
				font-size: 64px;
			}
			h2 {
				font-size: 52px;
				line-height: 1;
			}
			h3 {
				font-size: 44px;
				line-height: 1;
			}
			h4 {
				font-size: 36px;
				line-height: 1;
			}
		}
		&-description {
			&__title {
				max-width: 420px;
				font-size: 26px;
			}
			&__info {
				width: 420px;
			}
		}
		&-gallery {
			&__box {
				height: 340px;
			}
		}
	}
	.content {
		&-title {
			max-width: 486px;
			font-size: 44px;
		}
		&-text {
			margin-bottom: 0;
			h1 {
				font-size: 64px;
			}
			h2 {
				font-size: 52px;
				line-height: 1;
			}
			h3 {
				font-size: 44px;
				line-height: 1;
			}
			h4 {
				font-size: 36px;
				line-height: 1;
			}
		}
	}
}

@media screen and (max-width: 1199px) and (hover: none){
	.footer {
		&-menu {
			ul {
				li {
					ul {
						margin-top: 4px;
					}
				}
			}
		}
	}
}

