				}
			}
		}
		&-group {
			background: transparent;
			width: auto;
			margin-bottom: 16px;
			.news-box {
				&:not(:last-child) {
					margin-bottom: 16px;
				}
			}
		}
		&-box {
			flex-direction: column;
			height: auto;
			min-height: 346px;
			border-radius: 32px;
			&.news-big {
				height: 346px;
				.news-box {
					&__head {
						height: auto;
						&:not(:last-child) {
							margin-bottom: 0;
						}
					}
					&__img {
						border-radius: 32px;
					}
					&__title {
						max-width: 380px;
						font-size: 20px;
						line-height: 1.1;
					}
					&__text {
						max-width: 320px;
						font-size: 14px;
					}
				}
			}
			&__head {
				width: auto;
				height: 108px;
				&:not(:last-child) {
					margin-bottom: 10px;
				}
			}
			&__img {
				border-radius: 24px;
			}
			&__body {
				padding: 14px;
			}
			&__content {
				max-height: 140px;
			}
			&__title {
				max-width: 380px;
			}
			&__text {
				max-width: 320px;
			}
		}
		&-other {
			&__inner {
				.title {
					text-align: center;
				}
			}
			.news-list {
				padding-top: 12px;
			}
		}
	}
	.article {
		margin-top: 0;
		&-title {
			max-width: 480px;
			font-size: 32px;
			line-height: 1;
			&:not(:last-child) {
				margin-bottom: 16px;
			}
		}
		&-date {
			&:not(:last-child) {
				margin-bottom: 44px;
			}
		}
		&-img,
		&-description,
		&-gallery,
		&-text,
		&-blockquote {
			&:not(:last-child) {
				margin-bottom: 44px;
			}
		}
		&-img {
			height: auto;
			min-height: 180px;
			padding-top: 55.05%;
		}
		&-text {
			h1{
				&:not(:last-child) {
					margin-bottom: 24px;
				}
			}
			h2,
			h3,
			h4,
			h5,
			h6 {
				&:not(:last-child) {
					margin-bottom: 16px;
				}
			}
			h1 {
				font-size: 40px;
			}
			h2 {
				font-size: 36px;
				line-height: 1;
			}
			h3 {
				font-size: 32px;
				line-height: 1;
			}
			h4 {
				font-size: 28px;
				line-height: 1;
			}
			h5 {
				font-size: 24px;
				line-height: 1;
			}
			h6 {
				font-size: 18px;
			}
			p {
				&:not(:last-child) {
					margin-bottom: 24px;
				}
			}
			ul,
			ol {
				&:not(:first-child) {
					margin-top: 32px;
				}
				&:not(:last-child) {
					margin-bottom: 32px;
				}
				li {
					&:not(:last-child) {
						margin-bottom: 16px;
					}
					ul,
					ol {
						margin-top: 16px;
					}
				}
			}
		}
		&-description {
			&__title {
				font-size: 24px;
				&:not(:last-child) {
					margin-bottom: 32px;
				}
			}
			&__info {
				&-box {
					&:not(:last-child) {
						margin-bottom: 32px;
					}
				}
				&-title {
					font-size: 18px;
					&:not(:last-child) {
						margin-bottom: 8px;
					}
				}
			}
		}
		&-gallery {
			display: block;
			&__box {
				height: auto;
				min-height: 200px;
				width: auto;
				padding-top: 69.12%;
				&:not(:last-child) {
					margin-right: 0;
					margin-bottom: 16px;
				}
			}
		}
		&-blockquote {
			&__tape {
				width: 80%;
			}
			&__box {
				padding: 24px 24px 0 24px;
				&-quote {
					position: relative;
					top: auto;
					left: auto;
					&:not(:last-child) {
						margin-bottom: 18px;
					}
				}
				&-title {
					font-size: 18px;
					&:not(:last-child) {
						margin-bottom: 16px;
					}
				}
				&-text {
					&:not(:last-child) {
						margin-bottom: 16px;
					}
				}
				&-foot {
					margin-top: 0;
				}
			}
		}
	}
	.content {
		&-title {
			max-width: 480px;
			font-size: 32px;
			line-height: 1;
		}
		&-text {
			h1, h2, h3, h4, h5, h6 {
				&:not(:first-child) {
					margin-top: 44px;
				}
			}
			h2 {
				font-size: 36px;
				line-height: 1;
			}
			h1 {
				font-size: 40px;
				&:not(:last-child) {
					margin-bottom: 24px;
				}
			}
			h3 {
				font-size: 32px;
			}
			h4 {
				font-size: 28px;
			}
			h5 {
				font-size: 24px;
			}
			h6 {
				font-size: 18px;
			}
			ul,
			ol {
				&:not(:last-child) {
					margin-bottom: 24px;
				}
			}
		}
	}
	.not-found {
		//min-height: calc(100vh - 86px);
		position: absolute;
		flex-direction: column;
		justify-content: center;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		.container {
			flex: 1 0 0;
		}
		&__inner {
			display: flex;
			min-height: 100%;
			margin-top: 0;
		}
		&__box {
			position: static;
			//min-height: calc(100vh - 88px - 86px);
			padding: {
				top: 42px;
				bottom: 100px;
			};
			.subtitle {
				max-width: 296px;
				font-size: 14px;
				line-height: 1.5;
				&:not(:last-child) {
					margin-bottom: 0;
				}
			}
			&-button {
				position: absolute;
				left: 0;
				right: 0;
				bottom: 0;
				.btn {
					width: 100%;
				}
			}
		}
	}
}

@media screen and (max-width: 679px) {
	.advantages {
		&-grid {
			grid-template-columns: repeat(1, 1fr);
		}
		&-box {
			min-height: 328px;
			padding-top: 0;
		}
	}
}

@media screen and (max-width: 575px) {
	.contacts {
		&-socials {
			&:not(:last-child) {
				margin-bottom: 44px;
			}
			&__list {
				display: block;
				height: auto;
			}
			&__box {
				margin-bottom: 0;
				&:not(:last-child) {
					margin-bottom: 10px;
				}
			}
		}
	}
}
