.footer {
	position: relative;
	padding: 12px 0 100px;
	&-top {
		position: relative;
		&:not(:last-child) {
			margin-bottom: 100px;
		}
		.footer {
			&-inner {
				align-items: flex-start;
			}
		}
	}
	&-bottom {
		position: relative;
	}
	&-inner {
		position: relative;
		display: flex;
		align-items: flex-end;
	}
	&-menu {
		position: relative;
		width: 492px;
		&:not(:first-child) {
			margin-left: 194px;
		}
		ul {
			display: flex;
			flex-direction: column;
			flex-wrap: wrap;
			height: 108px;
			li {
				position: relative;
				padding: 8px 0;
				&:hover {
					ul {
						transition: opacity 0.5s;
						visibility: visible;
						opacity: 1;
					}
					& > .icon {
						transform: rotateX(180deg);
					}
				}
				a,
				span:not(.icon) {
					color: $colorWhite;
					font-size: 15px;
					line-height: 1.33334;
					transition: color 0.5s;
					cursor: pointer;
				}
				a {
					&:hover {
						color: $colorBlue;
					}
				}
				.icon {
					font-size: 14px;
					margin-left: 4px;
					cursor: pointer;
					opacity: 0.4;
				}
				ul {
					position: absolute;
					background: $colorModal;
					display: block;
					top: 100%;
					left: 0;
					//min-width: 100%;
					min-width: 140px;
					height: auto;
					column-count: 1;
					border-radius: 18px;
					visibility: hidden;
					opacity: 0;
					z-index: 1;
					li {
						padding: 0;
						margin-bottom: 0;
						a {
							display: block;
							color: $colorWhite;
							padding: 8px 14px;
							transition: opacity 0.5s;
							opacity: 0.5;
							&:hover {
								color: $colorWhite;
								opacity: 1;
							}
						}
					}
				}
			}
		}
	}
	&-award {
		position: relative;
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-column-gap: 42px;
		grid-row-gap: 48px;
		width: 262px;
		line-height: 0;
		padding-top: 12px;
		margin-left: auto;
		&__box {
			position: relative;
			a {
				position: relative;
				display: inline-block;
				vertical-align: top;
				transition: opacity 0.5s;
				opacity: 0.2;
				&:hover {
					opacity: 0.8;
				}
			}
		}
	}
}
.copyright {
	position: relative;
	font-size: 11px;
	font-weight: 300;
	line-height: 1.636;
	letter-spacing: 0.02em;
	text-transform: uppercase;
	opacity: 0.6;
}
.develop {
	position: relative;
	font-size: 11px;
	font-weight: 300;
	line-height: 1.636;
	letter-spacing: 0.02em;
	text-transform: uppercase;
	margin-left: auto;
	opacity: 0.6;
	a {
		position: relative;
		display: inline-block;
		color: $colorWhite;
		&:before {
			content: '';
			position: absolute;
			background: $colorWhite;
			bottom: -2px;
			left: 0;
			width: 100%;
			height: 1px;
			opacity: 0.22;
		}
	}
}
